export default {
	data() {
		return {
			title: '添加产品',
			formData: {
				name: '',
				type: '',
				masterUnit: '',
				slaveUnit: '',
				unit: '',
				multiple: '',
				remarks: '',
			},
			disableSubmit: true,
			unitSwich: 0
		}
	},
	onShow() {
		let pages =  getCurrentPages()
		let curPage = pages[pages.length - 1]
		if (curPage.data.selectType) {
			this.formData.type = curPage.data.selectType.name
		}
		if (curPage.data.selectUnit) {
			if (this.unitSwich == 0) {
				this.formData.masterUnit = curPage.data.selectUnit.name
			} else {
				this.formData.slaveUnit = curPage.data.selectUnit.name
			}
		}
	},
	methods: {
		handleNavbarClickLeft() {
			uni.navigateBack()
		},
		handleSelectType() {
			uni.navigateTo({
				url: '../type/type'
			})
		},
		handleSelectMasterUnit() {
			this.unitSwich = 0
			uni.navigateTo({
				url: '../unit/unit'
			})
		},
		handleSelectSlaveUnit() {
			this.unitSwich = 1
			uni.navigateTo({
				url: '../unit/unit'
			})
		},
		handleSubmit() {
			uni.navigateBack()
		}
	},
	watch:{
		formData: {
			handler(val) {
				if (val.name && val.type && val.unit) {
					this.disableSubmit = false
				} else {
					this.disableSubmit = true
				}
			},
			deep: true
		}
	}
}
